.tool-folder {
    margin: auto;
    margin-top: 10px;
    background-color: #252830;
    width: auto;
    min-width: 250px;
    min-height: 200px;
    height: auto;
    border-left: .25vh solid #353b48;
    border-right: .25vh solid #353b48;
    border-bottom: .25vh solid #353b48;
}

.tool-folder-heading {
    width: 100%;
    height: auto;
    background-color: #0d1019;
    text-align: center;
    border-top: .25vh solid #353b48;
    border-bottom: .25vh solid #0984e3;
}

.tool-folder-heading h3 {
    color: white;
    font-size: 2.5vh;
    margin: 1.5vh;
}

.tool-folder-body {
    opacity: .75;
    display: inline-flex;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    padding: 1vh;
}

.tool-folder:hover .tool-folder-body{
    opacity: 1;
}


.tool-folder:hover {
    transform: scale(1.03); 
}