.side-nav {
    width: 20vw;
    border-right: .25vh solid #353b48;
    min-height: 90vh;
}

.searchDiv {
    height: auto;
    overflow-y: auto;
    border-bottom: .25vh solid #353b48;
}

.links-div {
    display: inline-flex;
    justify-content: center;
    color: #868991;
    width: 100%;
    height: auto;
    overflow-x: hidden;
    overflow-y: auto;
}

.links-div ul {
    padding-right: 10%;
    padding-left: 10%;
    width: 100%;
    min-width: 75px;
    list-style-type: none;
    margin-left: 1vh;
}

.links-div li {
    padding: .5vh;
    margin: 1vh;
    border: .1vh solid white;
}

.links-div a {
    text-decoration: none;
    color: #868991;
    width: 100%;
}

.links-div a:hover {
    color: #0984e3;
    cursor: pointer;
}


@media screen and  (max-width: 800px){
    .links-div li {
        font-size: 10px;
    }
}