@import url('https://fonts.googleapis.com/css?family=Audiowide');

.tool-link-div {
    display: inline-block;
    text-align: center;
    width: 30%;
    min-width: 100px;
    min-height: 100px;
    max-height: 125px;
    background-color: #0d1019;
    border-radius: 1vh;
    cursor: pointer;

}

.tool-name {
    display: inline-block;
    width: 100%;
    height: auto;
    padding-top: 5px;
    font-size: 18px;
    font-weight: 200;
    color: #0984e3;
}

.tool-icon-div {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 50%;
    min-height: 75px;
    max-height: 75px;
}

.tool-link-div:hover .tool-icon {
    color: #0984e3;
    transform: scale(1.2);
    transition: ease-in-out .25s;
}

.tool-icon {
    color: white;
}

.tool-text {
    display: none;
    position: absolute;
    font-weight: 100;
    top: 0vh;
    color: white;
    font-size: 12px;
    padding: 5px;
}

.tool-link-div:hover .tool-name {
    display: none;
}

.tool-link-div:hover .tool-text {
    display: block;

}

.tool-link-div:hover {
    transform: scale(1.1);
    transition: ease-in-out;
}

.tool-link-div:hover .tool-icon {
    display: none;
}