@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    margin: 0;
    padding: 0;
    font-family: 'Audiowide';
    font-weight: 500;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .endzone-nav {
    height: 10vh;
    width: 100vw;
    display: flex;
    background-color: #0a0d14;
    border-bottom: .5vh solid #353b48;
    transform: scale(1);
    animation: pulse 3s infinite;
    position: relative;    
    z-index: 1000;
  }
  
  @keyframes pulse {
      0% {
          box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  
      }
  
      70% {
          box-shadow: 0 0 0 .5vh rgba(0, 0, 0, 0);
      }
  
      100% {
          box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      border-bottom: .5vh solid #0984e3;
      }
  }
  
  .endzone-nav a{
    text-decoration: none;
    color: white;
    font-size: 2.25vh;
    padding-bottom: .5vh;
    border-bottom: solid .1vh #0984e3;
  }
  
  .endzone-nav a:hover {
    color: #0984e3;
    transition: all .3s ease-in-out;
    border-bottom: solid .1vh white;
  }
  
  .user-div button {
    position: relative;
    font-family: 'Audiowide', sans-serif;
    font-weight: 100;
    background-color: #0a0d14;
    color: white;
    border: 0;
    border-radius: .75vh;
    font-size: 2vh;
    padding: 1vh 1vw;
    cursor: pointer;
    height: 50%;
    top: 25%;
  }
  .endzone-nav button:hover {
    transform: scale(1.01); 
    animation: content_pulse 2s infinite;
  }
  
  .dropdown-content {
    position: relative;
    top: 50%;
    display: none;
    background-color: #252830;
    min-width: 10vw;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1000;
  }

/* Links inside the dropdown */
.dropdown-content a {
    float: none;
    color: #0984e3;
    padding: 1vh 1vw;
    text-decoration: none;
    display: block;
    text-align: left;
  }

    /* Add a grey background color to dropdown links on hover */
    .dropdown-content a:hover {
            background-color: #ddd;
    }

  /* Show the dropdown menu on hover */
  .user-div:hover .dropdown-content {
    display: block;
  }

  @keyframes content_pulse {
      0% {
          box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
          border: .1vh white;
      }
  
      33% {
          box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
      }
  
      66% {
          box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
      }
  
      100% {
          box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
      }
  }
  
  .endzone-nav button:hover {
    background-color: #0984e3; 
    transition: all .3s ease-in-out;
    transform: scale(1.1);
  }
  
  .endzone-nav .user-div {
    margin-left: auto;
    margin-bottom: auto;
    height: 100%;
    width: auto;
    padding-right: 1vw;
    padding-left: 1vw;
    background-color: #252830;
    border-left: .5vh solid #353b48;
    text-align: center;
  }
  
  .logo-container {
    width: auto;
    height: auto;
    margin: 1vh;

  }
  
  .logo-container .logo {
    width: 100%;
    max-width: 300px;
    height: 100%;
    padding: 0,0,0;
    cursor: pointer;
  }

  .nav-links {
    margin-left: 5vh;
    width: auto;
    display: inline-flex;
    align-items: center;
  }
  .endzone-nav ul {
    margin-top: 2.5vh;
  }

  .endzone-nav ul li{
    left: 0;
    list-style: none;
    margin: 2.5vh;
    margin-top: 3;
    display: inline-block;
    justify-content: space-evenly;  }


  @media screen and  (max-width: 800px){
    .logo-container {
        display: inline-flex;
        justify-content: center;
        width: 100%;
        margin: auto;
        margin-top: 1vh;
    }

    .nav-links {
      width: 100%;
      margin: 0;
    }
    
    .endzone-nav {
      display: inline-block;
      height: auto;
      width: 100%;
      min-width: 250px;
    }
  
    .endzone-nav ul {
      text-align: center;
      width: 90%;
      margin: auto;
      padding: 0;
    }

    .logo-container .logo {
      width: 100%;
      margin-right: 15%;
    }
    .endzone-nav a {
      padding: 0;
      margin: 0;
    }

    .endzone-nav .user-div {
      padding: 1vh;
      border-left: none;
    }
  
    .entry-scoreboard, .entry-header {
      padding: 0;
      border: 0;
      min-width: 250px;
    }

    .offensive-section, .defensive-section {
      border: 0;
      border-radius: 0;
    }
    body {
      padding-bottom: 10vh;
      min-width: 250px;
    }
  }